*,
*::before,
*::after {
  box-sizing: border-box;
  cursor: default;
}

* {
  -ms-overflow-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

ul, ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  color: $grey;
  text-decoration: none;
}

:focus {
  box-shadow: $shadow;
  outline: none;
}

::-webkit-scrollbar-track {
	-webkit-box-shadow: none;
  background-color: transparent;
  }

::-webkit-scrollbar {
	width: 6px;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
	background-color: rgba(0,0,0,0.3);
	-webkit-border-radius: 6px;
  border-radius: 6px;
  }

  ::-webkit-scrollbar:horizontal {
    display: none;
  }